import React, { Component } from 'react';
import { Table } from 'rsuite';
import { compact } from 'lodash/array';

const { Column, HeaderCell, Cell, Pagination } = Table;

class Elcap extends Component {

  constructor(props) {
    super(props);
    this.state = {
      data: [],
    }
  }

  componentDidMount() {
    fetch('./data/elcap.json')
      .then(response => response.json())
      .then(data => this.normalizeData(data))
      .then(data => this.setState({ data }));
  }

  normalizeData(data) {
    return data.map(item => {
      const data = compact(item.data.split(' '));
      const cap = data[0].replace('mkf', "uF");
      const volt = data[2].replace('v', "V");
      const series = data[data.length - 2];
      const size = data[data.length - 1].split('*');
      const d = size[0];
      const h = size[1];


      item.manufacture = item.manufacture.toLowerCase();
      item.rub = item.price * 75 * 1.3;
      item.rub2 = Math.round(item.price * 75 * 1.3 * 100) / 100;
      // item.rub2 = Math.round(item.price * 75 * 1.3);
      item.cap = cap;
      item.volt = volt;
      item.series = series;
      item.d = d;
      item.h = h;
      return item
    })
  }

  render() {
    return (
      <div style={{ padding: 20 }}>
        <div style={{ height: 100 }}>asd</div>
        <div style={{ height: "100%" }}>
          <Table
            virtualized
            height={700}
            affixHeader
            autoHeight
            data={this.state.data}
          >
            {/* <Column width={70} align="center" >
              <HeaderCell>Id</HeaderCell>
              <Cell dataKey="id" />
            </Column> */}

            {/* <Column width={330}>
              <HeaderCell>Data</HeaderCell>
              <Cell dataKey="data" />
            </Column> */}

            <Column width={100}>
              <HeaderCell>Manufacture</HeaderCell>
              <Cell dataKey="manufacture" />
            </Column>

            <Column width={100} align="center" >
              <HeaderCell>Series</HeaderCell>
              <Cell dataKey="series" />
            </Column>

            <Column width={100} align="center" >
              <HeaderCell>Cap, uF</HeaderCell>
              <Cell dataKey="cap" />
            </Column>

            <Column width={100} align="center" >
              <HeaderCell>Volt</HeaderCell>
              <Cell dataKey="volt" />
            </Column>

            <Column width={100} align="center" >
              <HeaderCell>Diameter</HeaderCell>
              <Cell dataKey="d" />
            </Column>

            <Column width={100} align="center" >
              <HeaderCell>Height</HeaderCell>
              <Cell dataKey="h" />
            </Column>

            <Column width={100} align="center" >
              <HeaderCell>Min order</HeaderCell>
              <Cell dataKey="package" />
            </Column>

            <Column width={100} align="center" >
              <HeaderCell>Price</HeaderCell>
              <Cell dataKey="price" />
            </Column>

            <Column width={100} align="center" >
              <HeaderCell>Price, RUB</HeaderCell>
              <Cell dataKey="rub" />
            </Column>

            <Column width={100} align="center" >
              <HeaderCell>Price, RUB</HeaderCell>
              <Cell dataKey="rub2" />
            </Column>


          </Table>

          <Pagination></Pagination>
        </div>
      </div>
    )
  }

}

export default Elcap;
